/* Auth.css */

/* Container for the entire authentication page */
.auth-container {
  min-height: 100vh;
  background-color: #f7f7f7; /* Light grey background */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

/* The card that holds the form elements */
.auth-card {
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
  padding: 40px 30px;
  text-align: center;
}

/* Company logo styling */
.auth-logo {
  max-width: 70%;
  height: auto;
  margin-bottom: 20px;
}

/* Title style (e.g., "Welcome Back" or "Create an Account") */
.auth-title {
  margin: 0;
  font-size: 24px;
  font-weight: bold;
  color: #333333;
}

/* Subtitle style under the title */
.auth-subtitle {
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 14px;
  color: #777777;
}

/* Labels for the input fields */
.auth-input-label {
  display: block;
  text-align: left;
  font-size: 14px;
  color: #555555;
  margin-bottom: 5px;
}

/* Input field styles */
.auth-input {
  width: 100%;
  height: 40px;
  border: 1px solid #cccccc;
  border-radius: 5px;
  padding: 0 10px;
  font-size: 14px;
  margin-bottom: 15px;
  box-sizing: border-box;
}

/* Placeholder text styling */
.auth-input::placeholder {
  color: #aaa;
}

/* Button styling */
.auth-button {
  width: 100%;
  height: 45px;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 10px;
}

.auth-button:hover {
  background-color: #0056b3;
}

/* Forgot password link styled as a button for consistency */
.forgot-password {
  background: none;
  border: none;
  color: #007bff;
  font-size: 14px;
  text-decoration: none;
  cursor: pointer;
  margin-bottom: 15px;
}

.forgot-password:hover {
  text-decoration: underline;
}

/* General link styling (used in “Create one” and “Login here”) */
.auth-link {
  color: #007bff;
  text-decoration: none;
  font-size: 14px;
}

.auth-link:hover {
  text-decoration: underline;
}

/* Styles for success messages */
.auth-message {
  background-color: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 15px;
  font-size: 14px;
}

/* Styles for error messages */
.auth-error {
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 15px;
  font-size: 14px;
}